@use "global" as *;

.page-top {
  position: fixed;
  bottom: rem(16);
  right: rem(15);
  width: rem(48);
  height: rem(48);
  background-color: $white;
  border-radius: 50%;
  z-index: 100;
  @include mq('md') {
    right: rem(20);
    width: rem(80);
    height: rem(80);
  }
}

.page-top img{
  transform: rotate(-90deg);
}
