@use "global" as *;

.sub-blog {
  position: relative;
  z-index: -100;
}

.sub-blog::before {
  content: "";
  position: absolute;
  top: rem(57);
  left: rem(14);
  width: rem(122);
  height: rem(55);
  background-image: url(/assets/images/common/bg_icon3.png);
  background-repeat: no-repeat;
  background-size: cover;
  z-index: -1;
  @include mq('md') {
    top: rem(107);
    left: calc(50% - min(48%, rem(620)));
    width: rem(306);
    height: rem(138);
  }
}

.sub-blog__inner {
  position: relative;
  z-index: -1;
  @include mq('md') {
    padding-right: rem(15);
  }
}

.sub-blog__container {
  margin-top: rem(46);
  @include mq('md') {
    margin-top: rem(144);
    display: flex;
    justify-content: center;
  }
}

.sub-blog__cards {
  padding-left: rem(6);
  padding-right: rem(6);
  @include mq('md') {
    padding-left: 0;
    padding-right: 0;
  }
}

.sub-blog__side-contents {
  margin-top: rem(80);
  @include mq('md') {
    margin-top: initial;
    margin-left: rem(74);
    width: 42%;
  }
}

.sub-blog__side-contents-inner {
  max-width: rem(520);
  width: 100%;
  margin: auto;
  display: grid;
  gap: rem(80);
  padding:  0 rem(26);
  @include mq('md') {
    max-width: rem(294);
    padding: initial;
  }
}

.sub-blog__pagenavi .wp-pagenavi {
  margin-top: rem(40);
  @include mq('md') {
    margin-top: rem(99);
  }
}

.sub-blog__pagenavi .wp-pagenavi .previouspostslink {
  margin-right: rem(40);
  @include mq('md') {
    margin-right: rem(50);
  }
}

.sub-blog__pagenavi .wp-pagenavi .nextpostslink {
  margin-left: rem(40);
  @include mq('md') {
    margin-left: rem(50);
  }
}
