  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.extend .wrap {
  display: block;
  opacity: 0;
  animation-name: extendAnimeSecond;
  animation-duration: 1s;
  animation-delay: 0.6s;
  animation-fill-mode: forwards;
}

