  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.extend::before {
  display: block;
  content: "";
  position: absolute;
  max-width: 31.25rem;
  width: 100%;
  height: 100%;
  background-color: #408F95;
  animation-name: bgRLextendAnime;
  animation-duration: 1s;
  animation-fill-mode: forwards;
}

@keyframes bgRLextendAnime {
  0% {
    transform: scaleX(0);
    transform-origin: right;
  }
  50% {
    transform: scaleX(1);
    transform-origin: right;
  }
  50.001% {
    transform-origin: left;
  }
  100% {
    transform: scaleX(0);
    transform-origin: left;
  }
}
